
import { ImageSetLayer, Place } from "@wwtelescope/engine";
import { applyImageSetLayerSetting } from "@wwtelescope/engine-helpers";
import { MiniDSBase } from "@minids/common";
import { defineComponent } from "vue";

class BackgroundImageset {
  public imagesetName: string;
  public displayName: string;

  constructor(displayName: string, imagesetName: string) {
    this.displayName = displayName;
    this.imagesetName = imagesetName;
  }
}

const skyBackgroundImagesets: BackgroundImageset[] = [
  new BackgroundImageset(
    "Optical (Terapixel DSS)",
    "Digitized Sky Survey (Color)"
  ),
  new BackgroundImageset(
    "Low-frequency radio (VLSS)",
    "VLSS: VLA Low-frequency Sky Survey (Radio)"
  ),
  new BackgroundImageset("Infrared (2MASS)", "2Mass: Imagery (Infrared)"),
  new BackgroundImageset("Infrared (SFD dust map)", "SFD Dust Map (Infrared)"),
  new BackgroundImageset("Ultraviolet (GALEX)", "GALEX (Ultraviolet)"),
  new BackgroundImageset(
    "X-Ray (ROSAT RASS)",
    "RASS: ROSAT All Sky Survey (X-ray)"
  ),
  new BackgroundImageset(
    "Gamma Rays (FERMI LAT 8-year)",
    "Fermi LAT 8-year (gamma)"
  ),
];

type ToolType = "crossfade" | "choose-background" | null;
type SheetType = "text" | "video" | null;

export default defineComponent({
  extends: MiniDSBase,

  props: {
    wtml: {
      type: Object,
      required: true
    },
    wwtNamespace: {
      type: String,
      required: true
    },
    url: {
      type: String,
      required: true
    },
    thumbnailUrl: {
      type: String,
      required: true
    },
    bgWtml: {
      type: String,
      required: true
    },
    bgName: {
      type: String,
      required: true
    }
  },

  data() {
    return {
      layers: {} as Record<string,ImageSetLayer>,
      cfOpacity: 50, // out of 100
      title: "Compare JWST and Hubble images of Carina!",
      description: "Pan, zoom, and compare the images using AAS WorldWide Telescope.",
      hashtags: ["jwst", "hubble", "wwt", "carina", "unfoldtheuniverse"],
      ready: false,
      backgroundImagesets: [] as BackgroundImageset[],
      showSplashScreen: false,
      showLayers: true,
      layersLoaded: false,
      showResetTooltip: false,
      showTextTooltip: false,
      showVideoTooltip: false,
      sheet: null as SheetType,
      currentTool: "crossfade" as ToolType,
      tab: 0,
      networks: [
        { name: "facebook", color: "#1877f2", text: "Share" },
        { name: "twitter", color: "#1da1f2", text: "Tweet" },
      ]
    }
  },

  created() {
    this.waitForReady().then(() => {

      this.backgroundImagesets = [...skyBackgroundImagesets];

      const layerPromises = Object.entries(this.wtml).map(([key, value]) =>
        this.loadImageCollection({
          url: value,
          loadChildFolders: false
        }).then((folder) => {
          const children = folder.get_children();
          if (children == null) { return; }
          const item = children[0] as Place;
          const imageset = item.get_backgroundImageset() ?? item.get_studyImageset();
          if (imageset === null) { return; }
          this.addImageSetLayer({
            url: imageset.get_url(),
            mode: "autodetect",
            name: key,
            goto: false
        }).then((layer) => {
          this.layers[key] = layer;
          applyImageSetLayerSetting(layer, ["opacity", 0.5]);
        });
      }));

      Promise.all(layerPromises).then(() => {
        this.resetView();
        this.layersLoaded = true;
      });
      

      this.loadImageCollection({
        url: this.bgWtml,
        loadChildFolders: true,
      }).then((_folder) => {
        this.curBackgroundImagesetName = this.bgName;
        this.backgroundImagesets.unshift(
          new BackgroundImageset("unWISE", "unwise")
        );
      });

    });
  },

  computed: {
    crossfadeOpacity: {
      get(): number {
        return this.cfOpacity;
      },
      set(o: number) {
        if (this.layers.hubble) {
          applyImageSetLayerSetting(this.layers.hubble, ["opacity", 1 - 0.01 * o]);
        }
        if (this.layers.jwst) {
          applyImageSetLayerSetting(this.layers.jwst, ["opacity", 0.01 * o]);
        }
        this.cfOpacity = o;
      }
    },

    curBackgroundImagesetName: {
      get(): string {
        if (this.wwtBackgroundImageset == null) return "";
        return this.wwtBackgroundImageset.get_name();
      },
      set(name: string) {
        this.setBackgroundImageByName(name);
      }
    },

    hashtagString() {
      return this.hashtags.join(",");
    },

    isLoading(): boolean {
      return !this.ready;
    },

    mobile(): boolean {
      return this.smallSize && this.touchscreen;
    },

    showTextSheet: {
      get(): boolean {
        return this.sheet === 'text';
      },
      set(_value: boolean) {
        this.selectSheet('text');
        this.showTextTooltip = false;
      }
    },

    showVideoSheet: {
      get(): boolean {
        return this.sheet === "video";
      },
      set(value: boolean) {
        this.selectSheet('video');
        if (!value) {
          const video = document.querySelector("#info-video") as HTMLVideoElement;
          video.pause();
        }
        this.showVideoTooltip = false;
      }
    },

    smallSize(): boolean {
      return this.$vuetify.display.smAndDown;
    }
  },

  methods: {
    closeSplashScreen() {
      this.showSplashScreen = false;
    },

    selectSheet(name: SheetType) {
      if (this.sheet == name) {
        this.sheet = null;
        this.$nextTick(() => {
          this.blurActiveElement();
        });
      } else {
        this.sheet = name;
      }
    },

    resetView(instant = true) {
      const D2R = Math.PI / 180.0;
      const imageset = this.layers.jwst.get_imageSet();
      this.gotoRADecZoom({
        raRad: D2R * imageset.get_centerX(),
        decRad: D2R * imageset.get_centerY(),
        zoomDeg: 0.8595,
        rollRad: 1.799,
        instant: instant,
      });
    }
  },

  watch: {
    showLayers(show: boolean) {
      Object.values(this.layers).forEach(layer => {
        applyImageSetLayerSetting(layer, ["enabled", show]);
      });
    },
    layersLoaded(loaded: boolean) {
      if (loaded) {
        this.ready = true;
      }
    },
    ready(r: boolean) {
      if (r) {
        this.showSplashScreen = true;
      }
    }
  }

});
